import { Component, Input, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { MatChipInputEvent } from '@angular/material/chips';
import { COMMA, ENTER } from '@angular/cdk/keycodes';
import { RegisterService } from 'src/app/services/register.service';
import { ToastService } from 'src/app/services/toast.service';
import { Router } from '@angular/router';
import {MatDialog} from '@angular/material/dialog';
import { ImageCropComponent } from '../../../components/common/image-crop/image-crop.component';

import { ActivatedRoute } from '@angular/router';
import { DialogBoxComponent } from '../../common/dialog-box/dialog-box.component';

@Component({
  selector: 'app-update-profile',
  templateUrl: './update-profile.component.html',
  styleUrls: ['./update-profile.component.scss']
})
export class UpdateProfileComponent implements OnInit {
userid : string;
croppedImage: any;
public studentForm: FormGroup;
selectable = true;
removable = true;
addOnBlur = true;
image: any;
readonly separatorKeysCodes = [ENTER, COMMA] as const;
subjects =  [];

  constructor( private router: Router, private aRoute: ActivatedRoute, public dialogBox: MatDialog, private fb: FormBuilder, private registerService: RegisterService, private toastService: ToastService) { }

  ngOnInit(): void {
    this.aRoute.queryParams.subscribe((res:any) =>{
      this.userid = res?.userid;
      
    });
    console.log('current-userid', this.userid)
    this.studentForm = this.fb.group({
      firstName: ['', Validators.required],
      middleName: [''],
      lastName: ['', Validators.required],
      addressLine1: ['', Validators.required],
      addressLine2: ['', Validators.required],
      city: ['', Validators.required],
      country: [''],
      school: ['', Validators.required],
      stream: [''],
      subjects: [''],
      contact: ['', Validators.required],
      identityNumber: ['', Validators.required],
      password:['', Validators.required],
      veryfyPassword:['', Validators.required]
    },{
      validator:MustMatch('password', 'veryfyPassword')
    });
  }

  openImageCropDialog(){
    const dialgoRef = this.dialogBox.open(ImageCropComponent, {
      disableClose: true,
    width: '800px',
    height: '600px'
    });
    dialgoRef.afterClosed().subscribe(data =>{
      this.image = data.image;
    });
  }

  add(event: MatChipInputEvent): void {
    const value = (event.value || '').trim();
    if (value) {
      this.subjects.push(value);
    }
    event.chipInput!.clear();
  }
  remove(sub: string): void {
    const index = this.subjects.indexOf(sub);
    if (index >= 0) {
      this.subjects.splice(index, 1);
    }
  }

updateStudentProfile() {
   let updateProfile: UpdateProfile = ({
      firstName: this.studentForm.value.firstName,
      middleName: this.studentForm.value.middleName,
      lastName: this.studentForm.value.lastName,
      address1: this.studentForm.value.addressLine1,
      address2: this.studentForm.value.addressLine2,
      city: this.studentForm.value.city,
      country: this.studentForm.value.country,
      school: this.studentForm.value.school,
      stream: this.studentForm.value.stream,
      subjects: this.subjects,
      contact: this.studentForm.value.contact,
      identityNumber: this.studentForm.value.identityNumber,
      password: this.studentForm.value.password,
      image: this.image,
      updated:new Date().getTime()
    });
    this.registerService.UpdateStudent(this.userid,updateProfile).then((response: any) =>{
     this.toastService.show(response?.message, { classname: 'bg-success text-light', delay: 6000 });
     if(response?.status=="success"){
       this.registerDoneDialog();
     }
    });
  }

registerDoneDialog(){
  const dialgoRef = this.dialogBox.open(DialogBoxComponent,{
    disableClose: true,
    width:'400px',
    height:'300px'
  });
}
  

}

export class UpdateProfile implements User.Student {
  firstName: string;
  middleName: string;
  lastName: string;
  address1: string;
  address2: string;
  city: string;
  country: string;
  school: string;
  stream: string;
  subjects: any;
  contact: string;
  password: string;
  image: any;
  identityNumber: string;
  updated: number;
}



export function MustMatch(controlName: string, matchingControlName: string) {
  return (formGroup: FormGroup) => {
      const control = formGroup.controls[controlName];
      const matchingControl = formGroup.controls[matchingControlName];

      if (matchingControl.errors && !matchingControl.errors.mustMatch) {
          return;
      }
      if (control.value !== matchingControl.value) {
          matchingControl.setErrors({ mustMatch: true });
      } else {
          matchingControl.setErrors(null);
      }
  }
}
