import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { Router } from '@angular/router';
import { RegisterService } from 'src/app/services/register.service';
import { ToastService } from '../../../services/toast.service';
import { NgxSpinnerService } from "ngx-bootstrap-spinner";

@Component({
  selector: 'app-register-page',
  templateUrl: './register-page.component.html',
  styleUrls: ['./register-page.component.scss']
})
export class RegisterPageComponent implements OnInit {
  public registerForm: FormGroup;

  constructor(private spinner: NgxSpinnerService, private router: Router, private fb: FormBuilder, private registerService: RegisterService, public toastService: ToastService) {

  }


  ngOnInit(): void {
    this.registerForm = this.fb.group({
      email: ['',[ Validators.required, Validators.email]]
    });
  }
  public createUser() {
    this.spinner.show();
    let response: any;
    let newUser: NewUser = ({
      email: this.registerForm.value.email,
      created: new Date().getTime()
    });
 
    this.registerService.CreateNewUser(newUser).then((res: any) => {
      this.spinner.hide();
      response = res;
      if (response?.status == 'error') {
        this.toastService.show(response?.message, { classname: 'bg-danger text-light', delay: 6000 });
      } else if (response?.status == 'success') {
        this.toastService.show(`${response?.message}. please wait...`, { classname: 'bg-success text-light', delay: 6000 });
        this.router.navigateByUrl('/login').then(res => {
          console.log(res)
        });


      }
    });

  }



}

class NewUser implements User.LoginUser {
  email: string;
  created: Number;
}