import { Injectable } from '@angular/core';
import firebase from 'firebase/app';
//import {AppUser} from "../models/app-user";
import { AngularFirestore, AngularFirestoreCollection } from "@angular/fire/firestore";
import { Observable, Subject } from "rxjs";
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { timeout } from 'rxjs/operators';
import { environment } from '../../environments/environment.prod';
import AppUser = User.UserProfile;
import LoginUser = User.LoginUser;


@Injectable({
  providedIn: 'root'
})
export class UserService {
  private subject$ = new Subject<any>();
  
  constructor(private http: HttpClient) {
   
  }

  login(user: LoginUser): Observable<any> {
    let url: string = `${environment.apiUrl}/api/user/login`;
    let headers = new HttpHeaders();
    headers = headers.append('Content-Type', 'application/json');
    headers = headers.append('Accept', 'application/json');
    return this.http.post<any>(url, user).pipe(timeout(10000));
  }

  getUserDetails(id: string):Observable<any>{
    let url: string = `${environment.apiUrl}/api/user/get/${id}`;
    let headers = new HttpHeaders();
    headers = headers.append('Content-Type', 'application/json');
    headers = headers.append('Accept', 'application/json');
    return this.http.get<any>(url).pipe(timeout(10000));
  }

  getLocalUser():AppUser{
    const user = JSON.parse(localStorage.getItem('user')) || undefined;
    return user;
  }


}
