import { Component, OnInit, OnDestroy } from '@angular/core';
import { NgxSpinnerService } from "ngx-bootstrap-spinner";
import LogUser = User.LoginUser;
import AuthUser = User.UserProfile;
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { ToastService } from 'src/app/services/toast.service';
import { Router } from '@angular/router';
import { UserService } from 'src/app/services/user.service';
import { Subscription } from 'rxjs';

@Component({
  selector: 'app-login-page',
  templateUrl: './login-page.component.html',
  styleUrls: ['./login-page.component.scss']
})
export class LoginPageComponent implements OnInit, OnDestroy {
loginForm:FormGroup;
authUser: AuthUser;
hide = true;
loginSubscription: Subscription;
  constructor(private router: Router, public toastService: ToastService, private fb: FormBuilder, private spinner: NgxSpinnerService, private userService: UserService) { 
    
  }
 
  ngOnDestroy(): void {
    this.loginSubscription.unsubscribe();
  }

  ngOnInit(): void {
    this.loginForm = this.fb.group({
      email:['',[Validators.required, Validators.email]],
      password:['', Validators.required]
    })
  }



  login(){
    this.spinner.show();
    let loginUser: LogUser = ({
      email:this.loginForm.value.email,
      password:this.loginForm.value.password
    });
    this.loginSubscription = this.userService.login(loginUser).subscribe(
      (response: any) => {
        this.spinner.hide();
        if(response?.status === true){
         localStorage.setItem('user', JSON.stringify(response))
         this.router.navigateByUrl('/')
        }else{
        this.toastService.show(response?.message, { classname: 'bg-danger text-light', delay: 6000 });
        
        }
      });
  }

}
