import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { timeout } from 'rxjs/operators';
import {environment} from'../../environments/environment.prod'
@Injectable({
  providedIn: 'root'
})
export class RegisterService {

  constructor(private http: HttpClient) { }

  public CreateNewUser(user: any){
    return new Promise(resolve =>{
      let url: string = `${environment.apiUrl}/api/user/singup`;
      let headers = new HttpHeaders();
      headers = headers.append('Content-Type', 'application/json');
      headers = headers.append('Accept', 'application/json');
      this.http.post(url, JSON.stringify(user), { headers: headers }).pipe(timeout(10000)).subscribe((res) => {
            resolve(res)
      }, (err) => {
        console.log('error:', err);
      });
    });
  }

  public UpdateStudent(id: string, student: any){
    return new Promise(resolve =>{
      let url: string = `${environment.apiUrl}/api/user/update-profile/${id}`;
      let headers = new HttpHeaders();
      headers = headers.append('Content-Type', 'application/json');
      headers = headers.append('Accept', 'application/json');
      this.http.put(url, JSON.stringify(student), { headers: headers }).pipe(timeout(10000)).subscribe((res) => {
            resolve(res)
      }, (err) => {
        console.log('error:', err);
      });
    });
  }

}
